import request from '../../utils/request.js'
module.exports = {
	data(){
		return {
			userInfo: {
				
			},
			personalList: [
				{
					name: '我的订单',
					icon: 'icon-dingdan11'
				},
				{
					name: '我的拼团',
					icon: 'icon-pintuandingdan'
				},
				{
					name: '领红包',
					icon: 'icon-tubiaolunkuo-1'
				},
				{
					name: '我的积分',
					icon: 'icon-jifen3'
				},
				{
					name: '地址管理',
					icon: 'icon-dizhiguanli'
				},
				{
					name: '账号安全',
					icon: 'icon-dingdan11'
				},
				{
					name: '联系客服',
					icon: 'icon-zhanghaoanquan'
				},
				{
					name: '用户反馈',
					icon: 'icon-tubiaolunkuo-'
				},
				{
					name: '帮助中心',
					icon: 'icon-bangzhuzhongxin'
				},
				{
					name: '退还/售后',
					icon: 'icon-shouhou'
				}
			]
		}
	},
	mounted(){
		// 获取code
		wx.login({
			success: async (res) => {
				let code = res.code;
				// 将code发送给服务器端获取openid相关信息
				let token = await request('/getOpenId', {code})
				
				wx.setStorageSync('token', token)
			}
		})
		
	},
	onShow(){
		// 小程序钩子函数，页面显示
		console.log('onShow')
		// 用户授权以后， 可以通过wx.getUserInfo() 获取用户基本信息
		wx.getUserInfo({
			success:(res) => {
				console.log('获取用户信息成功')
				console.log(res)
				this.userInfo = res.userInfo
			},
			fail: () => {
				console.log('获取用户信息失败')
			}
		})
	},
	beforeUpdate(){
		// Vue实例的钩子函数， 页面即将更新，响应式数据变化，
		console.log('beforeUpdate')
	},
	methods: {
		toLogin(){
			if(this.userInfo.nickName){
				return
			}
			wx.navigateTo({
				url: '/pages/login/login'
			})
		}
	}
	
}
